
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}


.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
.completed{
  text-decoration: line-through;
  color: green;

}
input{
  width: 95%;
  height: 27px;
 
}
.todo button{
  position: relative;
    /* bottom: 0px; */
