    /* top: -4%; */
    top: 2em;
    background-color: BLACK;
    COLOR: WHITE;
    WIDTH: 87PX;
    margin-left: 3em;
    
}
.btn-de{
  margin-left: -6px !important;
}
ul{
  list-style-type: none;
}
.todo{
  width: 298px;
  background: linear-gradient(180deg, #CDC5B4 0%, rgba(205, 197, 180, 0) 100%);
  border: 1px solid #000000;
  height: 109px;
  margin-bottom: 1em;
}
select{
  height: 34px;
  background: black;
  color: white;
  border: none;
  border-radius: 10%;
